@charset "utf-8";
/* CSS Document */

@font-face {
	font-family: 'FontAwesome';
	src: url("../fonts/fontawesome-webfont.eot");
	src: url("../fonts/fontawesome-webfont.eot?#iefix") format("embedded-opentype"), url("../fonts/fontawesome-webfont.woff2") format("woff2"), url("../fonts/fontawesome-webfont.woff") format("woff"), url("../fonts/fontawesome-webfont.ttf") format("truetype"), url("../fonts/fontawesome-webfont.svg#fontawesomeregular") format("svg");
	font-weight: normal;
	font-style: normal
}
.mobilelogo {
	float: left;
	width: 140px;
}
.fa {
	display: inline-block;
	font: normal normal normal 14px/1 FontAwesome;
	font-size: inherit;
	text-rendering: auto;
	-webkit-font-smoothing: antialiased;
	-moz-osx-font-smoothing: grayscale;
	transform: translate(0, 0)
}
.fa-navicon:before, .fa-reorder:before, .fa-bars:before {
	content: "ïƒ‰"
}
.uhs-header a.hdr-btn {
	color: #FFF;
	display: inline-block;
	font-size: 15px;
	font-weight: 600;
	line-height: 29px;
}
a.hdr-btn {
	-webkit-transition: background-color .06s linear, color .06s linear;
	transition: background-color .06s linear, color .06s linear;
	background-color: rgba(255,255,255,0.2);
	border-radius: 4px;
	font-weight: 600;
	padding: 0 10px;
	text-shadow: 1px 1px rgba(0,0,0,0.09);
	-webkit-box-shadow: 0 0 1px rgba(0,0,0,0.06), inset 0 0 0 1px rgba(0,0,0,0.15);
	box-shadow: 0 0 1px rgba(0,0,0,0.06), inset 0 0 0 1px rgba(0,0,0,0.15);
}
a.hdr-btn:nth-last-of-type(1), a.hdr-btn:nth-last-of-type(2) {
	margin-left: 5px
}
a.hdr-btn:hover {
	background-color: #d10101;
	text-decoration: none;
}
a.hdr-btn:focus, a.hdr-btn:active {
	background-color: rgba(78,142,248,0.2);
	color: #999;
	text-shadow: 1px 1px rgba(78,142,248,0.09)
}
.mobile-helper {
	background-color: #c9c9c9;
	color: #33383c;
	padding-bottom: 15px;
	padding-top: 15px
}
.uhs-header {
	background-color: #191a1c;
	border-bottom: 1px solid #333742;
	position: relative;
	padding-bottom: 7px;
	padding-top: 7px;
	margin-top: 0
}
.uhs-header a.hdr-link, .uhs-header a.hdr-btn {
	color: #FFF;
	display: inline-block;
	font-size: 15px;
	font-weight: 600;
	line-height: 29px
}
#home .uhs-header a.hdr-link:hover, #home .uhs-header a.hdr-link:active {
	color: #191a1c !important
}

@media(max-width:1200px) {
.uhs-header a.hdr-toggle i {
	vertical-align: middle
}
}

/*
@media(min-width:992px) {
.uhs-header a.hdr-toggle {
	display: none
}
.mobile-navigation {
	display: none
}
}
*/

@media(min-width:1200px) {
.uhs-header {
	left: 0;
	right: 0;
	top: 0;
	z-index: 9999
}
}

@media(min-width:1200px) {
.uhs-header {
	background-color: transparent;
	border-bottom: 0;
	position: absolute;
	margin-top: 36px
}
}

@media(max-width:1200px) {
/*
.uhs-header a.hdr-link {
	display: none
}
*/
.mobile-navigation {
	background-color: rgba(25,26,28,0.9);
	padding-bottom: 15px;
	padding-top: 15px
}
.mobile-navigation .col-xs-6 {
	margin-bottom: 10px
}
.mobile-navigation .col-xs-6:nth-last-of-type(1), .mobile-navigation .col-xs-6:nth-last-of-type(2) {
	margin-bottom: 0
}
.mobile-navigation a {
	-webkit-box-shadow: 0 0 1px rgba(0,0,0,0.06), inset 0 0 0 1px rgba(0,0,0,0.15);
	box-shadow: 0 0 1px rgba(0,0,0,0.06), inset 0 0 0 1px rgba(0,0,0,0.15);
	border-radius: 4px;
	color: #FFF;
	display: block;
	font-weight: 600;
	padding-bottom: 15px;
	padding-top: 15px;
	text-align: center;
	width: 100%;
	text-transform: uppercase; 
}
.mobile-navigation a.theme-bg-1:hover, .mobile-navigation a.theme-bg-2:hover, .mobile-navigation a.theme-bg-3:hover {
	background-color: #d10101;
	text-decoration: none;
}
.mobile-navigation a.theme-bg-1 {
	background-color: #191a1c;
}
.mobile-navigation a.theme-bg-2 {
	background-color: #434348;
}
.mobile-navigation a.theme-bg-3 {
	background-color: #090A0B;
}
}
